function HtmlView(allIssues) {
  this.allIssues = allIssues;
}

HtmlView.prototype = Object.create(ExcelView.prototype);

HtmlView.prototype.generate = function(options) {
    var maxFractionDigits = options.jiraConfig.maxFractionDigits;
    var hostBaseUrl = options.hostBaseUrl || '';
    var prepareLink = function(url, text) {
        return "<a href='" + hostBaseUrl + url + "'>" + text + "</a>";
    };
    var getColumnTitle = function (columnKey) {
        if (columnKey.user) {
            return columnKey.user.displayName || columnKey.user.name;
        } else if (columnKey.date) {
            return moment(columnKey.date).format('DD/MMM');
        } else if (columnKey.weekNumber) {
            return columnKey.weekNumber;
        } else if (columnKey.monthKey) {
            return moment(columnKey.keyValue).format('MMM');
        } else {
            if (['TimeTracking', 'TimeEntry', 'IssueTime'].indexOf(options.pivotTableType) >= 0) {
                return options.translations[columnKey.keyValue];
            } else {
                return columnKey.keyValue;
            }
        }
    };
    var pivotTable;
    if (options.pivotTable) {
        pivotTable = options.pivotTable;
        options.groupByField = options._groupByField;
        options.categorizeByField = options._categorizeByField;
    } else {
        pivotTable = PivotTableFactory.createPivotTable(options);
        for (var i in this.allIssues) {
            pivotTable.add(this.allIssues[i]);
        }
    }
    var columns = pivotTable.sortedColumns();
    var rows = pivotTable.sortedRows();
    var timeTrackingColumns = options.configOptions.timeTrackingColumns;
    var excel = '<style>\n';
    excel += '<!--br\n';
    excel += '    {mso-data-placement:same-cell;}\n';
    excel += '<!--table\n';
    var separators = (1234.5).toLocaleString().match(/\d+(\D)?\d+(\D)\d+/);
    excel += '    {mso-displayed-decimal-separator:"\\' + separators[2] + '";';
    excel += (separators[1] ? '\n    mso-displayed-thousand-separator:"\\' + separators[1] + '";' : '') + '}\n';
    excel += '-->\n';
    excel += '</style>\n';
    excel += "<table>\n";
    excel += "  <tr>\n";
    if (options.categorizeByField && options.groupByField) {
        excel += "    <td>Category</td>\n";
    }
    if (options.groupByField) {
        excel += "    <td>" + options.groupByFieldObject.name + "</td>\n";
    }
    var hideCommentColumn = options.compressed || options.groupByField || options.pivotTableType == 'TimeTracking';
    if (!options.groupByField || (options.groupByField && !options.compressed)) {
        excel += "    <td>Issue Type</td>\n";
        excel += "    <td>Parent</td>\n";
        excel += "    <td>Key</td>\n";
        excel += "    <td>Summary</td>\n";
        excel += "    <td>Priority</td>\n";
        if (!hideCommentColumn) {
            excel += "    <td>Comment</td>\n";
        }
    }
    for (var i in options.moreFields) {
        var moreField = options.moreFields[i];
        excel += "    <td>" + options.moreFieldsOptions.findOption(moreField).label + "</td>\n";
    }
    for (var i in columns) {
        excel += "    <td>" + getColumnTitle(columns[i].columnKey) + "</td>\n";
    }
    var hideTotalColumn = ["TimeTracking", "TimeBalance", "Timespent"].indexOf(options.pivotTableType) >=0;
    if (!hideTotalColumn) {
        excel += "    <td>Total</td>\n";
    }
    excel += "  </tr>\n";
    for (var i in rows) {
        excel += "  <tr>\n";
        var row = rows[i];
        var issue = row.rowKey.issue || row.rowKey._issue;
        if (options.categorizeByField  && options.groupByField) {
            excel += "    <td>" + row.rowKey._project + "</td>\n";
        }
        if (options.groupByField) {
            excel += "    <td>" + row.rowKey._field + "</td>\n";
        }
        if (row.rowKey.issue) {
            excel += "    <td>" + issue.fields.issuetype.name + "</td>\n";
            excel += "    <td>" + (issue.fields.parent ? prepareLink('/browse/' + issue.fields.parent.key, issue.fields.parent.key ) : "") + "</td>\n";
            excel += "    <td>" + prepareLink('/browse/' + issue.key, issue.key) + "</td>\n";
            excel += "    <td>" + prepareLink('/browse/' + issue.key, issue.fields.summary) + "</td>\n";
            excel += "    <td>" + this.fieldValue(issue, 'priority', options) + "</td>\n";
            if (!hideCommentColumn) {
                excel += "    <td>&nbsp</td>\n";
            }
        } else if (!options.compressed) { // groupByField
            excel += "    <td>&nbsp;</td>\n".repeat(5);
        }
        for (var i in row.moreFields) {
            // FIXME: getFormattedTime for time fields
            excel += "    <td>" + this.fieldValue(issue, row.moreFields[i], options) + "</td>\n";
        }
        for (var i in columns) {
            var columnName = columns[i].columnKey.keyValue;
            var entry = row.columns[columnName];
            if (columnName == '6progress') {
                var value = entry.value;
                if (value.timespent + value.estimate == 0) {
                    excel += "    <td></td>\n";
                } else {
                    var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent));
                    excel += "    <td>" + DrawHelper.formatDecimal(percents, maxFractionDigits) + "%</td>\n";
                }
            } else if (columnName == '5originalestimateremaining') {
                excel += "    <td>" + DrawHelper.getFormattedTime(entry.value.value, options.jiraConfig) + "</td>\n";
            } else if (["1timeoriginalestimate", "2esttimeremaining", "3timespent", "4diff"].indexOf(columnName) >= 0) {
                excel += "    <td>" + DrawHelper.getFormattedTime(entry.sum, options.jiraConfig) + "</td>\n";
            } else {
                excel += "    <td>" + DrawHelper.getFormattedTime(entry.sum, options.jiraConfig) + "</td>\n";
            }
        }
        if (!hideTotalColumn) {
            excel += "    <td>" + DrawHelper.getFormattedTime(row.sum, options.jiraConfig) + "</td>\n";
        }
        excel += "  </tr>\n";
        if (!options.compressed && (options.pivotTableType != 'TimeTracking' || options.groupByField)) {
            for (var j in row.data) {
                excel += "  <tr>\n";
                if (options.categorizeByField  && options.groupByField) {
                    excel += "    <td>&nbsp;</td>\n";
                }
                if (options.groupByField) {
                    excel += "    <td>&nbsp;</td>\n";
                }
                var detailedRow = row.data[j];
                var detailedIssue = detailedRow.rowKey._issue;
                if (detailedIssue) {
                    excel += "    <td>" + detailedIssue.fields.issuetype.name + "</td>\n";
                    excel += "    <td>" + (detailedIssue.fields.parent ? prepareLink('/browse/' + detailedIssue.fields.parent.key, detailedIssue.fields.parent.key ) : "") + "</td>\n";
                    excel += "    <td>" + prepareLink('/browse/' + detailedIssue.key, detailedIssue.key) + "</td>\n";
                    excel += "    <td>" + prepareLink('/browse/' + detailedIssue.key, detailedIssue.fields.summary) + "</td>\n";
                    excel += "    <td>" + this.fieldValue(detailedIssue, 'priority', options) + "</td>\n";
                } else {
                    excel += "    <td>&nbsp;</td>\n".repeat(5);
                }
                for (var i in row.moreFields) {
                    // FIXME: groupByField _issue moreFiels
                    excel += "    <td>&nbsp;</td>\n";
                }
                if (!hideCommentColumn) {
                  excel += "    <td>" + (detailedRow.worklog ? detailedRow.worklog.comment : "") + "</td>\n";
                }
                for (var i in columns) {
                    var columnName = columns[i].columnKey.keyValue;
                    var value = detailedRow.values[columnName];
                    if (columnName == '6progress') {
                        if (value.timespent + value.estimate == 0) {
                            excel += "    <td></td>\n";
                        } else {
                            var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent));
                            excel += "    <td>" + DrawHelper.formatDecimal(percents, maxFractionDigits) + "%</td>\n";
                        }
                    } else if (columnName == '5originalestimateremaining') {
                        excel += "    <td>" + DrawHelper.getFormattedTime(value.value, options.jiraConfig) + "</td>\n";
                    } else if (["1timeoriginalestimate", "2esttimeremaining", "3timespent", "4diff"].indexOf(columnName) >= 0) {
                        excel += "    <td>" + DrawHelper.getFormattedTime(value, options.jiraConfig) + "</td>\n";
                    } else if (detailedRow.columnKey.keyValue == columnName) {
                        excel += "    <td>" + DrawHelper.getFormattedTime(value, options.jiraConfig) + "</td>\n";
                    } else {
                        excel += "    <td>&nbsp;</td>\n";
                    }
                }
                if (!hideTotalColumn) {
                    excel += "    <td>&nbsp;</td>\n";
                }
                excel += "  </tr>\n";
            }
        }
    }
    excel += "  <tr>\n";
    excel += "    <td>Total</td>\n";
    if (options.categorizeByField && options.groupByField) {
        excel += "    <td>&nbsp;</td>\n";
    }
    if (options.groupByField) {
        excel += "    <td>&nbsp;</td>\n";
    } else {
        excel += "    <td>&nbsp;</td>\n".repeat(5);
    }
    if (!hideCommentColumn) {
        excel += "    <td>&nbsp;</td>\n";
    }
    for (var i in options.moreFields) {
        // FIXME: getFormattedTime for time fields
        excel += "    <td>" + this.fieldValue({fields: pivotTable.fieldTotals}, options.moreFields[i], options) + "</td>\n";
    }
    for (var i in columns) {
        var columnName = columns[i].columnKey.keyValue;
        var entry = pivotTable.totals[columnName];
        if (columnName == '6progress') {
            var value = entry.value;
            if (value.timespent + value.estimate == 0) {
                excel += "    <td></td>\n";
            } else {
                var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent));
                excel += "    <td>" + DrawHelper.formatDecimal(percents, maxFractionDigits) + "%</td>\n";
            }
        } else if (columnName == '5originalestimateremaining') {
            excel += "    <td>" + DrawHelper.getFormattedTime(entry.value.value, options.jiraConfig) + "</td>\n";
        } else if (["1timeoriginalestimate", "2esttimeremaining", "3timespent", "4diff"].indexOf(columnName) >= 0) {
            excel += "    <td>" + DrawHelper.getFormattedTime(entry.sum, options.jiraConfig) + "</td>\n";
        } else {
            excel += "    <td>" + DrawHelper.getFormattedTime(entry.sum, options.jiraConfig) + "</td>\n";
        }
    }
    if (!hideTotalColumn) {
        excel += "    <td>" + DrawHelper.getFormattedTime(pivotTable.sum, options.jiraConfig, options.pivotTableType == 'IssuePassedTimeByStatus' && !options.workingTimeInStatus) + "</td>\n";
    }
    excel += "  </tr>\n";
    excel += "</table>\n";
    return excel;
};
