function CsvView(allIssues) {
    this.allIssues = allIssues;
}

CsvView.dateTimeFormat = 'YYYY-MM-DD HH:mm:ss';

// see also PivotKey.GroupByField
CsvView.prototype.fieldValue = function(issue, fieldObject, options) {
    var NULL_VALUE = "";
    var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[([\s\S]*)?\]/;
    var maxFractionDigits = maxFractionDigits;

    var getNameOrKey = function(field) {
        var m;
        if (angular.isObject(field) && field.hasOwnProperty('name')) {
            return field.name;
        } else if (angular.isObject(field) && field.hasOwnProperty('key')) {
            var name = field.hasOwnProperty('fields') && field.fields.hasOwnProperty('summary') ? ' ' + field.fields.summary : '';
            return field.key + name;
        } else if (angular.isObject(field) && field.hasOwnProperty('value')) {
            return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
        } else if (angular.isString(field) && (m = field.match(sprintRegex))) {
            var splits = m[1].split(',');
            for (var i = 0; i < splits.length; i++) {
                var split = splits[i];
                if (split.indexOf('name=') == 0) {
                    return split.substr(5);
                }
            }
            return NULL_VALUE;
        } else {
            return field;
        }
    };

    var getValue = function(field) {
        if (TimesheetUtils.isDate(field)) {
            return moment(field).format(CsvView.dateTimeFormat);
        } else if (Array.isArray(field)) {
            var value = '';
            if (field.length > 0 && (field[0].inwardIssue || field[0].outwardIssue)) {
                value = field.map(function(v) {
                    if (v.inwardIssue) {
                        return v.type.inward + ' ' + getNameOrKey(v.inwardIssue);
                    } else {
                      return v.type.outward + ' ' + getNameOrKey(v.outwardIssue);
                    }
                }).join(', ');
            } else {
                field.forEach(function(v) {
                    if (value.length > 0) {
                        value += ', ';
                    }
                    value += getNameOrKey(v);
                });
            }
            return value;
        } else {
            return getNameOrKey(field);
        }
    };

    var fieldName = fieldObject.id || fieldObject;

    var getFieldValueObject = function(issue) {
        var value = issue.fields[fieldName];
        if (fieldObject && fieldObject.schema && fieldObject.schema.custom == 'com.pyxis.greenhopper.jira:gh-epic-link') {
            if (value) {
                value = {key: value};
            } else if (issue.fields.issuetype.name == 'Epic') {
                value = {key: issue.key};
            }
        }
        if (fieldName == 'project') {
            value = NULL_VALUE;
        }
        if (!value) {
            if (fieldName == 'parent' && options._groupByField == fieldName) {
                value = {key : issue.key};
            } else if (fieldName == 'projectcategory' && issue.fields.project.projectCategory) {
                value = issue.fields.project.projectCategory;
            } else {
                value = NULL_VALUE;
            }
        }
        return value;
    };

    this.field = getFieldValueObject(issue);
    var value = getValue(this.field);
    if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(fieldName) != -1 && value != NULL_VALUE) {
        value = DrawHelper.formatDecimal((value || 0) / 3600, maxFractionDigits);
    } else if (fieldName == 'timetrackingprogress') {
        var actual = issue.fields.timespent || 0;
        var remaining = issue.fields.timeestimate || 0;
        if (actual + remaining == 0) {
            value = "";
        } else {
            var percent = Math.round(actual * 100 / (actual + remaining));
            value = DrawHelper.formatDecimal(percent, maxFractionDigits) + '%';
        }
    } else if (fieldName == 'timetrackingestimate') {
        var actual = issue.fields.timespent || 0;
        var remaining = issue.fields.timeestimate || 0;
        value = DrawHelper.formatDecimal((actual + remaining) / 3600, maxFractionDigits);
    } else if (fieldName == 'timetrackingvariance') {
        var original = issue.fields.timeoriginalestimate || 0;
        var actual = issue.fields.timespent || 0;
        var remaining = issue.fields.timeestimate || 0;
        var estimate = actual + remaining;
        value = DrawHelper.formatDecimal((original - estimate) / 3600, maxFractionDigits);
    } else if (fieldName == 'timetrackingoriginalestimateremaining') {
        var original = issue.fields.timeoriginalestimate || 0;
        var actual = issue.fields.timespent || 0;
        value = DrawHelper.formatDecimal((original - actual) / 3600, maxFractionDigits);
    }
    return value;
};
CsvView.prototype.escape = function(value) {
    return '"' + value.replace(/"/g, '""') + '"';
};
CsvView.prototype.enquote = function(value) {
    return value.indexOf(',') >= 0 ? this.escape(value) : value;
};
/*
    Provides raw worklogs.

    Supported options: pivotTableType, monthView, startDate, endDate, user
    TODO? groups, sumSubTask
*/
CsvView.prototype.generate = function(options) {
    var maxFractionDigits = options.jiraConfig.maxFractionDigits;
    var pivotTable = PivotTableFactory.createPivotTable(options);
    var isTimeView = ["TimeTracking", "TimeBalance"].indexOf(options.pivotTableType) >=0;
    var timeColumns;
    if (options.pivotTableType == "TimeTracking") {
        timeColumns = options.configOptions.timeTrackingColumns;
    } else if (options.pivotTableType == "TimeBalance") {
        timeColumns = options.configOptions.timeBalanceColumns;
    }
    var csv = "Project,Issue Type,Key,Summary,Priority";
    if (options.pivotTableType == "TimeBalance") {
        csv += ",Changed";
    }
    for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i];
        if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(moreField.id) >= 0) {
            csv += "," + this.escape(moreField.name + ' (h)');
        } else if (['workeduser', 'workeddate'].indexOf(moreField.id) < 0) {
            csv += "," + this.escape(moreField.name);
        }
    }
    if (isTimeView) {
        for (var i in timeColumns) {
          if (timeColumns[i] == '6progress') {
              csv += "," + this.escape(options.translations[timeColumns[i]]);
          } else {
              csv += "," + this.escape(options.translations[timeColumns[i]] + ' (h)');
          }
        }
        csv += "\n";
    } else {
      csv += ",Date Started";
      if (options.pivotTableType == "IssuePassedTimeByStatus") {
          csv += ",Date Ended";
      }
      csv += ",Username,Display Name,Time Spent (h)";
      if (options.pivotTableType == "IssuePassedTimeByStatus") {
          csv += ",Status\n";
      } else {
          csv += ",Work Description\n";
      }
    }
    var totalTimeSpentSeconds = 0;
    for (var i in this.allIssues) {
        var pivotEntries = pivotTable.add(this.allIssues[i]);
        for (var i in pivotEntries) {
            var pivotEntry = pivotEntries[i];
            // rowKey is PivotKey.Issue
            var issue = pivotEntry.rowKey._issue || pivotEntry.rowKey.issue;
            var worklog = pivotEntry.rowKey.worklog || pivotEntry.worklog || {}; // for PassedTimeByStatus & TimeBalance
            csv += this.escape(issue.fields.project.name) + ",";
            csv += issue.fields.issuetype.name + ",";
            csv += issue.key + ",";
            csv += this.escape(issue.fields.summary) + ",";
            csv += this.escape(this.fieldValue(issue, 'priority', options)) + ",";
            if (options.pivotTableType == "TimeBalance") {
                csv += this.escape(worklog.comment || '') + ",";
            }
            for (var i = 0; i < options.moreFields.length; i++) {
                var moreField = options.moreFields[i];
                if (['workeduser', 'workeddate'].indexOf(moreField.id) < 0) {
                    if ('workcreated' == moreField.id) {
                        csv += moment(worklog.created).format(CsvView.dateTimeFormat) + ",";
                    } else {
                        csv += this.escape(String(this.fieldValue(issue, moreField, options))) + ",";
                    }
                }
            }
            if (isTimeView) {
                for (var i in timeColumns) {
                    if (i > 0) {
                        csv += ",";
                    }
                    if (timeColumns[i] == '6progress') {
                        // see timeTracking filter
                        var value = pivotEntry.values['6progress'];
                        if (value.timespent + value.estimate == 0) {
                            csv += "";
                        } else {
                            var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
                            csv += this.enquote(DrawHelper.formatDecimal(percents, maxFractionDigits) + '%');
                        }
                    } else if (timeColumns[i] == '5originalestimateremaining') {
                        csv += this.enquote(DrawHelper.formatDecimal(pivotEntry.values[timeColumns[i]].value / 3600, maxFractionDigits));
                    } else {
                        csv += this.enquote(DrawHelper.formatDecimal(pivotEntry.values[timeColumns[i]] / 3600, maxFractionDigits));
                    }
                }
                csv += "\n";
            } else {
                csv += moment(worklog.workDate).format(CsvView.dateTimeFormat) + ",";
                if (options.pivotTableType == "IssuePassedTimeByStatus") {
                    csv += moment(worklog.toDate).format(CsvView.dateTimeFormat) + ",";
                }
                csv += worklog.author.name + ",";
                csv += (this.escape(worklog.author.displayName) || '') + ",";
                csv += this.enquote(DrawHelper.formatDecimal(worklog.timeSpentSeconds / 3600, maxFractionDigits)) + ",";
                csv += this.escape((worklog._issue ? worklog._issue.key + ' ' : '') +
                    (options.pivotTableType == "IssuePassedTimeByStatus" ? pivotEntry.columnKey.keyValue : worklog.comment)) + "\n";
                totalTimeSpentSeconds += worklog.timeSpentSeconds;
            }
        }
    }

    var comas = "";
    var moreFieldsLength = 0;
    for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i];
        if (['workeduser', 'workeddate'].indexOf(moreField.id) < 0) {
            moreFieldsLength++;
        }
    }
    var colspan = isTimeView ? 5 : 8;
    if (options.pivotTableType == "TimeBalance") {
        colspan++;
    }
    for (var i = 0; i < colspan + moreFieldsLength; i++) {
        comas += ",";
    }
    if (options.pivotTableType == "IssuePassedTimeByStatus") {
        comas += ",";
    }
    csv += "Total" + comas;
    if (isTimeView) {
        for (var i in timeColumns) {
            if (i > 0) {
                csv += ",";
            }
            if (timeColumns[i] == '6progress') {
                // see timeTracking filter
                var value = pivotTable.totals['6progress'].value;
                if (value.timespent + value.estimate == 0) {
                    csv += "";
                } else {
                    var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
                    csv += this.enquote(DrawHelper.formatDecimal(percents, maxFractionDigits) + '%');
                }
            } else if (timeColumns[i] == '5originalestimateremaining') {
                csv += this.enquote(DrawHelper.formatDecimal(pivotTable.totals[timeColumns[i]].value.value / 3600, maxFractionDigits));
            } else {
                csv += this.enquote(DrawHelper.formatDecimal(pivotTable.totals[timeColumns[i]].sum / 3600, maxFractionDigits));
            }
        }
        csv += "\n";
    } else {
        csv += this.enquote(DrawHelper.formatDecimal(totalTimeSpentSeconds / 3600, maxFractionDigits)) + "\n";
    }
    return csv;
};
CsvView.prototype.download = function(options) {
    var csvFile = '\ufeff' + this.generate(options);
    var blob = new Blob([csvFile], {encoding:"utf-8", type: 'application/vnd.ms-excel;charset=utf-8;'});
    saveAs(blob, "exportData.csv");
};
